@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
body {
  margin: 0;
}



.App {
  display: grid;
  grid-gap: 20px;
  grid-template-areas:
    "header header"
    "sidebar main";
  grid-template-rows: 70px 1fr;
  grid-template-columns: 240px 3fr;

  width: 100%;
  height: 100vh;
  background-color: rgb(14, 14, 13);
}

.Header {
  background-color: #24292e;
  grid-area: header;
}
.Header_logo {
  display: flex;
  justify-content: space-between;
}
.Header_logo_img_i {
  margin-top: 7px;
  margin-right: 15px;
  margin-left: 40px;
  width: 45px;
  height: 50px;
}
.Header_logo_text {
  margin-top: 15px;
  font-size: 22px;
  color: whitesmoke;
  font-family: 'Roboto', sans-serif;
}

.ee{
  display: flex;
  justify-content: space-between;
}
.Time {
  color: rgb(252, 252, 252);
  font-size: 30px;
  font-family: 'Roboto', sans-serif;
  margin-top: 7px;
  margin-right: 55px;
  width: 130px;
  height: 50px;
  border: 2px solid #fff;
  border-radius: 5px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.Sidebar {
  background-color: #081627;
  border-radius: 10px;
  grid-area: sidebar;
}

.Sidebar_nav {
  text-align: left;
}

.Sidebar_nav ul li {
  list-style: none;
}

.Sidebar_nav ul li a {
  font-family: "Montserrat", sans-serif;
  color: rgb(214, 214, 214);
  text-decoration: none;
  font-size: 30px;
  line-height: 1.5;
}

.Sidebar_nav ul li a:active {
  color: red;
}


.App_block {
  display: flex;
  justify-content: space-around;
}

.Todo {
  grid-area: main;
  font-size: 77px;
}

.AppTodo {
  display: flex;
  justify-content: center;
  align-items: baseline;
  background-color:  rgba(6, 103, 167, 0.623);
  border-radius: 12px;
  height: 600px;
  width: 1100px;
}

.TodoContent {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.TodoHeader {
  margin-left: 90px;
  font-size: 40px;
  font-family: "Montserrat", sans-serif;
  font-weight: 400;
  text-decoration: underline;
  color: white;
}

.TodoInput {
  display: flex;
  flex-direction: row;
}

.Input {
  height: 55px;
  width: 350px;
  border-radius: 10px;
  font-size: 35px;
}
.btn-in {
  height: 60px;
  width: 130px;
  border-radius: 10px;
  margin-left: 10px;
}

.btn-del {
  height: 55px;
  width: 130px;
  border-radius: 10px;
  margin-left: 15px;
}

.Output {
  font-size: 40px;
  list-style-type: disc;
  color: white;
}

.News_block {
  display: flex;
  justify-content: space-around;
}

.News {
  background-color: #081627;
  grid-area: main;
  height: 600px;
  width: 1150px;
  font-size: 55px;
  border-radius: 10px;
}
.NewsCard {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
}

.Card_title {
  margin-left: 25px;
  font-size: 25px;
  font-family: 'Roboto', sans-serif;
}

.Card_1,.Card_2,.Card_3,.Card_4,.Card_5,.Card_6 {
  margin-top: 60px;
  width: 300px;
  height: 200px;
  background-color: rgb(163, 206, 235);
  border-radius: 10px;
}


.contacts {
  display: flex;
  justify-content: center;
  margin-top: 55px;
  

}
.icon {
  width: 60px;
  height: 60px;
  background: rgba(255, 255, 255, 0);
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  margin-right: 20px;
  margin-bottom: 20px;
  border-radius: 5px;
  overflow: hidden;
  transition: -webkit-transform 0.5s;
  transition: transform 0.5s;
  transition: transform 0.5s, -webkit-transform 0.5s;
}

.icon img {
  width: 60px;
}

.icon a {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  opacity: 0;
  transition: 0.5s;
}

.icon a:hover {
  opacity: 1;
}

.icon:hover {
  -webkit-transform: translateY(-8px);
          transform: translateY(-8px);
}

